
import { PropType, defineComponent } from 'vue'
import { WithdrawAccount, CIB20100312_REQ_BODY, CIB20100312_RES } from '@/shared/type/CIB20100312.type'
import { IbUserDetail } from '@/shared/type/CIB20100311.type'
import { CIB20100335_REQ_BODY, CIB20100335_RES } from '@/shared/type/CIB20100335.type'
import { modalController } from '@ionic/vue';
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";

export default defineComponent({
  name: "cib201003ss05",

  props: {
    data: {
      type: Object as PropType<IbUserDetail>,
      required: true,
    },
    currentTab: {
      type: String,
      required: true,
    }
  },

  data() {
    const userID = this.$route.query.userID;
    const customerNo = this.$route.query.customerNo;
    const approvalTap = this.$route.query.tab;
    return {
      userID,
      customerNo,
      approvalTap,
      mainTransferAccount: "",
      withdrawAccounts: [] as WithdrawAccount[],
      isEditable: false,
      approvalRes: {} as any,
			operationMode: 'normal' as screenMode,
      tempMainAccount:''        
    };
  },

  watch: {
    data(customerData: IbUserDetail) {
      if(this.approvalType == ''){
        this.mainTransferAccount = customerData.userInfo.mainTransferAccount;
        this.tempMainAccount = customerData.userInfo.mainTransferAccount;
      }else{
        if(this.approvalTap == 'CIB201003SS05'){
          this.$approval.getAuthorizationDetail().then( (authDetail: APM20200113_RES) => {
            let authDetailParsed = JSON.parse(authDetail.body.requestContent);
            this.mainTransferAccount = authDetailParsed.mainTransferAccount
            this.approvalRes = authDetail.body;
          })
          this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
        }
      }
    },
    
    async currentTab(tabTittle: string){
      if(tabTittle == 'cib201003ss05.main_transfer_account'){
        await this.getWithdrawAccounts();
      }
    },

    isEditable(isAllowTabChange) {
      this.$emit('isBeingEdited', isAllowTabChange)
    }
  },

  computed: {
		approvalType(): string {
			return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
		},
  },

  mounted(){
    this.getWithdrawAccounts();
  },

  methods: {
    onClickOperation(btn: string) {
      switch (btn) {
        case "edit":
          this.isEditable = true;
          break;

        case "cancel": 
        this.mainTransferAccount = this.tempMainAccount;
          this.isEditable = false;
          break;

        case "list":
          this.$router.push('/corporate-internet-banking/subscription');
          break;

        case "submit": 
          this.$dialog.onOpen({
            content: `${this.$t("message.would_you_like_to_save")}`,
            dialogMode:"Question",
            callback:()=>{
              this.handleSubmit();
              modalController.dismiss();
            }
          });
          break;
      }
    },

    async getWithdrawAccounts(){
      const reqBody: CIB20100312_REQ_BODY = {
        customerNo: `${this.customerNo}`,
        userID: `${this.userID}`,
      };

      const responseData = await this.$request.getRequest('CIB20100312', reqBody, false) as CIB20100312_RES;
      this.withdrawAccounts = responseData.body.list;
    },

    async handleSubmit() {
      const reqBody: CIB20100335_REQ_BODY ={
        userID: `${this.userID}`,
        customerNo: `${this.customerNo}`,
        mainTransferAccount: this.mainTransferAccount
      };

      const responseData = await this.$request.getRequest('CIB20100335', reqBody, false, 'CIB201003SS05') as CIB20100335_RES;
      if(this.approvalType){
        this.isEditable = false;
        this.$emit("isReseTButton", false);
      }

      if(responseData.header.result){
        this.isEditable = false;
        this.$emit("isReseTButton", false);
        this.$dialog.onOpen({
          content: `${this.$t("message.update_success")}`,
          dialogMode:"Success",
          callback: ()=>{
            modalController.dismiss();
          }
        })
      }
    }
  }
})
