
import { PropType, defineComponent } from 'vue'
import { IbUserDetail } from '@/shared/type/CIB20100311.type'
import { CIB20100332_REQ_BODY, CIB20100332_RES } from '@/shared/type/CIB20100332.type'
import { modalController } from '@ionic/vue';
import { AuthorizationDetail } from '@/shared/type/APM20200113.type';
import { screenMode } from "@/shared/type/Approval.type";

export default defineComponent({
  name: "cib201003ss02",

  props: {
    data: {
      type: Object as PropType<IbUserDetail>,
      required: true
    },
    userId: {
      type: String,
      required: true
    },
    approvalPayload: {
      type: Object as PropType<AuthorizationDetail>,
      required: false
    },
    currentTab: {
      type: String,
      required: true,
    },
  },

  data() {
    const userID = this.$route.query.userID;
    const customerNo = this.$route.query.customerNo;
    const approvalTap = this.$route.query.tab;
    return {
      userID,
      customerNo,
      approvalTap,
      tempData: {
        customerNo: "",
        resetPassword: {
          email: "",
          userPasswordErrorCount: 0
        }
      },
      isEditable: false,
      isResetPasswaord: false,
      isWrongEmailFormat: false,
      operationMode: 'normal' as screenMode,
      tempEmail:'',
      tempUserPasswordErrorCount:0
    }
  },

  watch: {
    data(customerData: IbUserDetail) {
      if(this.approvalType == ''){
        this.tempData = JSON.parse(JSON.stringify({ 
          customerNo: customerData.userInfo.customerNo, 
          resetPassword: { 
            email: customerData.userInfo.email, 
            userPasswordErrorCount: customerData.userInfo.userPasswordErrorCount 
          } 
        }))
        this.tempEmail = customerData.userInfo.email;
        this.tempUserPasswordErrorCount = customerData.userInfo.userPasswordErrorCount;
      }else if(this.approvalPayload){
        if(this.approvalTap == 'CIB201003SS02'){
          let authDetailParsed = JSON.parse(this.approvalPayload?.requestContent)
            this.tempData.resetPassword.email = authDetailParsed.email;
            this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
          }
        }
    },

    'tempData.resetPassword.email':function(newEmailValue){
      if(newEmailValue){
        this.isWrongEmailFormat = false;
      }
    },

    isEditable(isAllowTabChange) {
      this.$emit('isBeingEdited', isAllowTabChange)
    }
  },

  computed: {
		approvalType(): string {
			return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
		},
  },

  methods: {
    onClickOperation(btn: string) {
      switch (btn) {
        case "edit":
          // in this reset password doesn't need summit button so we check and summit in case edit
          if(this.currentTab == 'cib201003ss02'){
            this.$dialog.onOpen({
              dialogMode: "Question",
              content: `${this.$t("message.would_you_like_to_send_password")}`,
              callback: async () => {
                this.handleSendPassword();
                modalController.dismiss();
              }
            })
          }
          break;

        case "cancel":
          this.tempData.resetPassword.email = this.tempEmail;
          this.tempData.resetPassword.userPasswordErrorCount = this.tempUserPasswordErrorCount;
          this.isEditable = false;
          break;

        case "list":
          this.$router.push('/corporate-internet-banking/subscription');
          break;

        case "submit":
          if(this.tempData.resetPassword.email && this.handleValidateEmailFormat()){
            this.handleSendPassword();
          }else{
            this.isResetPasswaord = true;
            this.handleValidateEmailFormat() == true ? this.isWrongEmailFormat = false : this.isWrongEmailFormat = true; // display error format message
          }
          break;
      }
    },

    async handleSendPassword() {
      let reqBody: CIB20100332_REQ_BODY = {
        customerNo: `${this.customerNo}`,
        userID: `${this.userID}`,
        email: this.tempData.resetPassword.email
      };

      // ---- Adapter: CIB20100332_Adapter_IBResetPassword_Modify
      const responseData = await this.$request.getRequest('CIB20100332', reqBody, false, 'CIB201003SS02') as CIB20100332_RES;
      if(this.approvalType){
        this.isEditable = false;
        this.$emit("isReseTButton", false);
      }

      if(responseData.header.result){
        this.tempData.resetPassword.userPasswordErrorCount = 0;
        // this.isEditable = false;
        // this.$emit("isReseTButton", false);
        this.$dialog.onOpen({
          content: `${this.$t("message.submit_successfully")}`,
          callback:()=>{
            modalController.dismiss();
          }
        })
      }
    },

    handleValidateEmailFormat(){
      let validEmailFormat = false;
      validEmailFormat = this.$validate.validateEmail(this.tempData.resetPassword.email);
      validEmailFormat ? this.isWrongEmailFormat = false : this.isWrongEmailFormat = true;
      return validEmailFormat;
    }
  }
})
