
import { defineComponent, ref } from 'vue'
import baseTab from '@/shared/components/Tab/BaseTab.vue'
import baseTabPan from '@/shared/components/Tab/BaseTabPan.vue'
import BreadCrumb from "@/shared/components/BreadCrumb/BreadCrumb.vue";
import cib201003ss01 from '../cib201003ss01/cib201003ss01.vue'
import cib201003ss02 from '../cib201003ss02/cib201003ss02.vue'
import cib201003ss03 from '../cib201003ss03/cib201003ss03.vue'
import cib201003ss04 from '../cib201003ss04/cib201003ss04.vue'
import cib201003ss05 from '../cib201003ss05/cib201003ss05.vue'
import cib201003ss06 from '../cib201003ss06/cib201003ss06.vue'
import cib201003ss08 from '../cib201003ss08/cib201003ss08.vue'
import cib201003ss09 from '../cib201003ss09/cib201003ss09.vue'
import cib201003ss10 from '../cib201003ss10/cib201003ss10.vue'
import cib201003ss11 from '../cib201003ss11/cib201003ss11.vue'
import cib201003ss12 from '../cib201003ss12/cib201003ss12.vue'
import { CIB20100311_REQ_BODY, CIB20100311_RES, IbUserDetail,UserInfo, UserTransferLimit,TransferAuthorizationHour } from '@/shared/type/CIB20100311.type'
import { WithdrawAccount } from '@/shared/type/CIB20100312.type'
import { modalController } from '@ionic/vue';
import CustomerInformation from "@/shared/components/CustomerInformation/CustomerInformation.vue";
import {CIB20100114_REQ_BODY, CIB20100114_RES, CustomerInfoInquiryDetail} from "@/shared/type/CIB20100114.type";
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";

export default defineComponent({
	name: 'cib201003sc00',
	components: {
		baseTab,
		baseTabPan,
		BreadCrumb,
		cib201003ss01,
		cib201003ss02,
		cib201003ss03,
		cib201003ss04,
		cib201003ss05,
		cib201003ss06,
		cib201003ss08,
		cib201003ss09,
		cib201003ss10,
		cib201003ss11,
		cib201003ss12,
		CustomerInformation
	},
	data() {
		const customerNo = this.$route.query.customerNo;
		const tabHeaders = ref([
			{ name: "cib201003ss01.lock_unlock_service", disable: false },
			{ name: "cib201003ss02.reset_password", disable: false },
			{ name: "cib201003ss03.security_media_sms_phone", disable: false },
			{ name: "cib201003ss04.transfer_limit", disable: false },
			{ name: "cib201003ss05.main_transfer_account", disable: false },
			{ name: "cib201003ss06.withdraw_account", disable: false },
			{ name: "cib201003ss08.re_activiate", disable: false },
			{ name: "cib201003ss09.ib_policy_configuration", disable: false },
			{ name: "cib201003ss10.transfer_authorization", disable: false },
			{ name: "cib201003ss12.ib_subscription_file_upload", disable: false },
		]);
		const ibUser = ref<IbUserDetail>({
			userInfo: {} as UserInfo,
			transferAuthorizationHour: {} as TransferAuthorizationHour,
			userTransferLimit: [] as UserTransferLimit[],
		});

		const userID = ref();
		return {
			ibUser,
			customerNo,
			withdrawAccount: [] as WithdrawAccount[],
			customerInformation:  {} as CustomerInfoInquiryDetail,
			userID,
			tabHeaders,
			currentTab: "",
			currentTabScreen: "",
			allowTabChange: true,
			isFetchDataSuccess: true,
			isEditable: false,
			isDisabledSendPassword: false,
			approvalRes: {} as any,
			operationMode: 'normal' as screenMode,
			componentKey: ''
		}
	},


	async created() {
		if(this.approvalType == ''){
			this.userID = this.$route.query.userID;
			this.customerNo = this.$route.query.customerNo;
			await this.onGetUserSubscriptionDetail(`${this.userID}`,`${this.customerNo}`);
			await this.getCustomerInfo(`${this.customerNo}`);
			
		}else{
			await this.$approval.getAuthorizationDetail().then( async (authDetail: APM20200113_RES) => {
				let authDetailParsed = JSON.parse(authDetail.body.requestContent);
				this.approvalRes = authDetail.body;
				await this.onGetUserSubscriptionDetail(authDetailParsed.userID,authDetailParsed.customerNo);
				await this.getCustomerInfo(authDetailParsed.customerNo);
				this.userID = this.$route.query.userId;
			});
			this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
		}
		
	},

	computed: {
		approvalType(): string {
			return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
		},

		queryTabId() : string {
			return this.$route.query.tab?.toString() || ''
		},

		customerType(): string {
			return this.$codeConverter.convertValue('subscriptionCustomerType', this.ibUser.userInfo.customerCorpIndTypeCode);
		},
	
        getResponseHeaderResult():boolean {
			return this.$store.getters["SubsciptionModule/getResponseHeaderResult"]; // get value from store (getter) such as true | false 
		}
	},


	watch: {
		allowTabChange(isAllow) {
			if (!isAllow) {
				this.allowTabChange = false;
			} else {
				this.allowTabChange = true;
			}
		},

		currentTab(tabValue){
			const getScreenName = tabValue.split('.');
			this.currentTab = getScreenName[0];
		}
	},

	methods: {

		async getCustomerInfo(customerNo:string){
            const reqBody:CIB20100114_REQ_BODY = {
                customerNo: customerNo
            };

            const responseData = (await this.$request.getRequest("CIB20100114", reqBody , false)) as CIB20100114_RES;
            this.customerInformation = responseData.body;
        },

		onClickOperation(btn: string) {
			type componentType = typeof cib201003ss01 | typeof cib201003ss02 | typeof cib201003ss03 | typeof cib201003ss04 | typeof cib201003ss05 | typeof cib201003ss06 | typeof cib201003ss08 | typeof cib201003ss09 | typeof cib201003ss10 | typeof  cib201003ss12
            switch (btn) {
                case "edit": 
					if(this.currentTab == 'cib201003ss02'){
						(this.$refs[this.currentTab] as InstanceType<componentType>).onClickOperation(btn);
						this.isEditable = false;
					}else{
						(this.$refs[this.currentTab] as InstanceType<componentType>).onClickOperation(btn);
						this.isEditable = true;
					}
                    break;

                case "cancel": 
					this.$dialog.onOpen({
						title: `${this.$t("message.alert")}`,
						content: `${this.$t("message.are_you_sure_you_want_to_cancel_without_saving_data")}`,
						showCancel: true,
						btnLabel: `${this.$t("common.confirm")}`,
						callback: () => {
							(this.$refs[this.currentTab] as InstanceType<componentType>).onClickOperation(btn);
							// this.onGetUserSubscriptionDetail(),
							this.isEditable = false;
							modalController.dismiss()
                        }
                    });
                    break;

                case "list": 
                    this.$router.push('/corporate-internet-banking/subscription');
                    break;

                case "submit": 
					(this.$refs[this.currentTab] as InstanceType<componentType>).onClickOperation(btn);
                    break;
            }
        },

		handleBackToSubScriptionLists(){
			this.$router.push('CIB201001SC00');
		},

		resetButtonEvent(value){
			this.isEditable = value;
		},

		handleBeforeTabLeave(title) {
			this.componentKey = title;
			// if (title !== this.currentTab) {
			// 	dialogService.onOpen({
			// 		title: `${this.$t("message.alert")}`,
			// 		content: `${this.$t("message.save_changes_before_switch_tab")}`,
			// 		showCancel: false,
			// 		callback: () => {
			// 			modalController.dismiss();
			// 		}
			// 	});
			// }
		},

		handleTabChange(isBeingEdited: boolean) {
			this.allowTabChange = this.approvalType === '' ? !isBeingEdited :false ;
		},

		handleCurrentTab(newValue): string | void{
			// this.currentTab = newValue || "Lock/Unlock Service";
			if(this.queryTabId !== ''){
				switch(this.queryTabId){
					case 'CIB201003SS01':
						this.currentTab = 'cib201003ss01.lock_unlock_service'
						return 'cib201003ss01.lock_unlock_service'
					case 'CIB201003SS02':
						this.currentTab = 'cib201003ss02.reset_password'
						return 'cib201003ss02.reset_password'
					case 'CIB201003SS03':
						this.currentTab = 'cib201003ss03.security_media_sms_phone'
						return 'cib201003ss03.security_media_sms_phone'
					case 'CIB201003SS04':
						this.currentTab = 'cib201003ss04.transfer_limit'
						return 'cib201003ss04.transfer_limit'
					case 'CIB201003SS05':
						this.currentTab = 'cib201003ss05.main_transfer_account'
						return 'cib201003ss05.main_transfer_account'
					case 'CIB201003SS06':
						this.componentKey = 'cib201003ss06.withdraw_account'
						this.currentTab = 'cib201003ss06.withdraw_account'
						return 'cib201003ss06.withdraw_account'
					case 'CIB201003SS07':
						this.currentTab = 'cib201003ss01.lock_unlock_service'
						return 'cib201003ss01.lock_unlock_service'
					case 'CIB201003SS08':
						this.currentTab = 'cib201003ss08.re_activiate'
						return 'cib201003ss08.re_activiate'
					case 'CIB201003SS09':
						this.currentTab = 'cib201003ss09.ib_policy_configuration'
						return 'cib201003ss09.ib_policy_configuration'
					case 'CIB201003SS10':
						this.currentTab = 'cib201003ss10.transfer_authorization'
						return 'cib201003ss10.transfer_authorization'
					case 'CIB201003SS12':
						this.currentTab = 'cib201003ss12.ib_subscription_file_upload'
						return 'cib201003ss12.ib_subscription_file_upload'
				}
			this.currentTab.split('.')[0];
			}else{
				this.currentTab = newValue;
			}
			
		},

		async onGetUserSubscriptionDetail(userID: string, customerNo: string) {
			const reqBody: CIB20100311_REQ_BODY = {
				userID: userID,
				customerNo: customerNo
			};
			// ---- Adapter: CIB20100311_Adapter_IBUserSubscription_InquiryDetail
			const responseData = await this.$request.getRequest('CIB20100311', reqBody, false) as CIB20100311_RES;
			let serviceStatusCode = responseData.body.userInfo.serviceStatusCode;
			serviceStatusCode == '02' || serviceStatusCode == '09' ? this.isDisabledSendPassword = true : this.isDisabledSendPassword = false;
		
			if (responseData.header.result === false) {
				this.isFetchDataSuccess = false;
			} else {
				this.ibUser = JSON.parse(JSON.stringify(responseData.body));
				this.isFetchDataSuccess = true;
			}
		},
	}
})
