
import { defineComponent, PropType } from "vue";
import { modalController } from '@ionic/vue';
import { CIB20100116_REQ_BODY, CIB20100116_RES, fileInformation } from '@/shared/type/CIB20100116.type';
import { CIB2010033A_REQ_BODY, CIB2010033A_RES } from '@/shared/type/CIB2010033A.type';
import { screenMode } from "@/shared/type/Approval.type";
import { AuthorizationDetail } from '@/shared/type/APM20200113.type';
import { FILE_TYPE } from '@/shared/constants/common.const';
import { FileSize } from "@/shared/services/FileSizeService";

const fileSize = new FileSize();

export default defineComponent({
  name: "cib201003ss12",
  props: {
    approvalPayload: {
      type: Object as PropType<AuthorizationDetail>,
      required: false
    },
    currentTab: {
      type: String,
      required: true,
    }
  },

  data() {
    const getFile = null;
    const getImage = null;
    const userID = this.$route.query.userID;
    const customerNo = this.$route.query.customerNo;
    const fileInfo = {
      fileId: '',
      filePath: '',
      transactionDate: '',
    }
    return {
      userID,
      getFile,
      getImage,
      fileInfo,
      customerNo,
      attachFile: '',
      fileName: '',
      fileSize: '',
      operationMode: 'normal' as screenMode,
      fileList: [] as fileInformation[],
      tempFileList: [] as fileInformation[],
      isEditable: false,
      isEmptyFile:false,
      removeFileButton: false,
      isShowPlaceholder: false
    }
  },

  watch: {
    async approvalPayload(payload){
      if(this.currentTab == 'CIB201003SS12'){
        let authDetailParsed = JSON.parse(payload.requestContent)
        authDetailParsed.fileList.map((item: fileInformation)=>{
          this.fileInfo.fileId = item.fileId 
          this.fileInfo.filePath = item.filePath
        })
        const responseFile = await this.$file.getFileInfo(this.fileInfo.fileId); // get file information
        this.fileName = responseFile?.data.body.originalFileName;
        this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
      }
    },

    isEditable(isAllowTabChange) {
      this.$emit('isBeingEdited', isAllowTabChange)
    },

    'fileInfo.fileId':function(fileID){
      if(fileID){
        this.isShowPlaceholder = false;
      }else{
        this.isShowPlaceholder = true;
      }
    }
  },

  computed:{
    approvalType(): string {
			return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
		},
  },

  mounted(){
    this.getFileList(`${this.userID}`);
  },

  methods:{
    async getFileList(userID: string){
      const reqBody: CIB20100116_REQ_BODY = {
        userID: userID
      };
    
      const responseData = await this.$request.getRequest('CIB20100116', reqBody, false) as CIB20100116_RES; // get list of files

      responseData.body.list.map((item)=>{
        this.fileList.push({
          fileId: item.fileId,
          filePath: item.filePath
        })
        return this.fileInfo = item;
      });

      
      if(this.fileInfo.fileId){
        const responseFile = await this.$file.getFileInfo(this.fileInfo.fileId); // get file information
        this.fileName = responseFile?.data.body.originalFileName;
        this.fileInfo.fileId = responseFile?.data.body.fileId;
      }else{
        this.isShowPlaceholder = true;
      }
    },

    onClickOperation(btn: string) {
      switch (btn) {
        case "edit": 
          this.isEditable = true;
          if(this.fileList.length == 0){
            this.isShowPlaceholder = true;
            this.removeFileButton = false;
          }else{
            this.removeFileButton = true;
          }
          break;

        case "cancel": 
          this.isEditable = false;
          this.getFileList(`${this.userID}`);
          this.removeFileButton = false;
          break;

        case "list":
          this.$router.push('/corporate-internet-banking/subscription');
          break;

        case "submit":
            this.$dialog.onOpen({
            content: `${this.$t("message.would_you_like_to_save")}`,
            dialogMode:"Question",
              callback:()=>{
                this.handleUpdateFile();
                modalController.dismiss();
              }
            })
          break;
      }
    },

    handleRemoveFile(){
      this.fileInfo.fileId = '';
      this.fileInfo.filePath = '';
      this.getFile = null;
      this.fileName = '';
      this.removeFileButton = false;
      this.isShowPlaceholder = true;
      this.isEmptyFile = true
    },

    async handleUploadFile(event: any) {
      this.isEmptyFile = false
      const files = event.target.files;
      const file = files[0];

      this.getFile = file;
      this.fileName = file.name;

      if (file.size > 10485760) {
        this.$dialog.onOpen({
          content: this.$t('message.file_size_must_not_exceed_10MB'),
          dialogMode: "Warning",
          callback: () => {
            modalController.dismiss()
          },
        });
      }
      else{
        this.removeFileButton = true
      const files = event.target.files;
      const file = files[0];
      if (file.size > 10485760) {
        this.fileName = ''
        this.fileInfo.fileId = ''
        this.$dialog.onOpen({
          content: this.$t('message.file_size_must_not_exceed_10MB'),
          dialogMode: "Warning",
          callback: () => {
            modalController.dismiss()
          },
        });
      } else {
        this.getFile = file;
        const size = fileSize.countFileSize(file.size);
        this.fileSize = size;
        const reader = new FileReader();
        reader.readAsDataURL(file);
        this.fileName = file.name;
        this.isShowPlaceholder = false;
      }

      const duplicatedFile = document.getElementById('actual-btn') as HTMLInputElement;
      duplicatedFile.value = '';
      }
    },

    async handleUpdateFile(){   
      // this condition work when we click remove icon and it will remove all field in fileList to empty array
      if (this.getFile === null && this.isEmptyFile) {
        for (let i = 0; i < this.fileList.length; i++) {
          await this.$file.updateFileStatus(this.fileList[i].fileId, FILE_TYPE.INACTIVE)
          for (let i = 0; i < this.fileList.length; i++) {
            const index = this.fileList.indexOf(this.fileList[i]);
            this.fileList.splice(index, 1);
          }
        }
      }

      if (this.getFile !== null && !this.isEmptyFile) {
        if(this.fileList.length == 0 ){
          const res = await this.$file.uploadFile([this.getFile], "CIB")
          for (let i = 0; i < res.data.body.fileList.length; i++) {
            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
            this.fileList.push({
              filePath: resFileInfo.data.body.filePath,
              fileId: res.data.body.fileList[i].fileId
            })
          }
        }else if (this.fileList.length > 0){
          const res = await this.$file.uploadFile([this.getFile], "CIB")
          for (let index = 0; index < this.fileList.length; index++) {
            await this.$file.updateFileStatus(this.fileList[index].fileId, FILE_TYPE.INACTIVE);
            for (let i = 0; i < res.data.body.fileList.length; i++) {
              await this.$file.getFileInfo(res.data.body.fileList[i].fileId).then(item =>
                Object.assign(this.fileList[index], { fileId: res.data.body.fileList[i].fileId, filePath: item.data.body.filePath })
              )
            }
          }
        }
      }

      const reqBody: CIB2010033A_REQ_BODY = {
        userID: `${this.userID}`,
        customerNo: `${this.customerNo}`,
        fileList: this.fileList
      };

      const responseData = await this.$request.getRequest('CIB2010033A', reqBody, false, 'CIB201003SS12') as CIB2010033A_RES;

      if(this.approvalType){
        this.isEditable = false;
        this.removeFileButton = false;
        this.$emit("isReseTButton", false);
      }

      if(responseData.header.result){
        this.isEditable = false;
        this.removeFileButton = false;
        this.$emit("isReseTButton", false);
        this.$dialog.onOpen({
          content: this.$t("message.update_success"),
          dialogMode:"Success",
          callback:()=>{
            modalController.dismiss();
          }
        })
      }else{
        console.log(responseData.header.error_text);
      }
    },
  }
});
